// кнопки
.btn-my {
  @include btn;
  padding: 12px;
  position: relative;
  overflow: hidden;
  z-index: 60;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: -25%;
    margin-left: auto;
    margin-right: auto;
    width: 150%;
    height: 100%;
    background: linear-gradient(to left, rgba(255,255,255,.0), rgba(255,255,255,.8), rgba(255,255,255,.0)) no-repeat -2em 0%;
    background-size: 4em 100%;
    transform: skewX(-40deg);
  }
}
// Блик на кнопках
.blink_on:after{
  transition: 1.5s linear;
  background-position: 130% 0;
}



